.hero {
  height: 100vh;
  background-color: blue;
  color: white;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  background-image: linear-gradient(rgba(95, 95, 95, 0.45), rgba(37, 19, 136, 0.45)), url(../../assets/background.jpeg);
}

.hero-contents {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  line-height: 4em;
}

.hero-arrow {
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 3em;

  a {
    color: white;
  }
}
